module.exports = "<p>Je suis une espèce d'agglomérat de plein de choses. Je fais de l'illustration, de la 3D (surtout de la sculpture), un peu de graphisme, et je joue à Pokemon. Je suis timide aussi. Mais je suis surtout un furieux optimiste.</p>\n<p>Sur ce blog, vous trouverez surtout des explorations et articles sur le développement. Ce qui me botte ce sont les interfaces, plutôt du côté du web. En ce moment je tourne beaucoup autour de l'écosystème React. Ça ne m'empêche pas de faire des écarts parce qu'au fond, la techno, on s'en fout un peu. C'est sûrement ce qui fait que je n'ai pas peur de faire du JS ou du PHP.</p>\n<p>Vous pouvez me trouver à Toulouse, à <a href=\"https://www.occitech.fr/\">Occitech</a>, mais aussi sur <a href=\"https://twitter.com/JulienPradet\">Twitter</a> ou à l'occasion de meetups. N'hésitez pas à me contacter. C'est toujours un plaisir.</p>\n"

